.header-library {
  height: 230px;
  background-color: #0000008f;
  background-image: url("../my-library-mob.0902cf4e.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  justify-content: space-between;
  align-items: baseline;
  padding: 40px 20px;
  display: flex;
  position: relative;
}

@media (min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2x) {
  .header-library {
    background-image: url("../my-library-mob-x2.895b425f.jpg");
  }
}

@media screen and (min-width: 768px) {
  .header-library {
    height: 220px;
    background-image: url("../my-library-tab.715c34d8.jpg");
    padding: 40px 34px;
  }
}

@media screen and (min-width: 768px) and (min-device-pixel-ratio: 2), screen and (min-width: 768px) and (min-resolution: 192dpi), screen and (min-width: 768px) and (min-resolution: 2x) {
  .header-library {
    background-image: url("../my-library-tab-x2.98f716a9.jpg");
  }
}

@media screen and (min-width: 1280px) {
  .header-library {
    height: 215px;
    background-image: url("../my-library-desk.e8781471.jpg");
    padding: 40px 20px;
  }
}

@media screen and (min-width: 1280px) and (min-device-pixel-ratio: 2), screen and (min-width: 1280px) and (min-resolution: 192dpi), screen and (min-width: 1280px) and (min-resolution: 2x) {
  .header-library {
    background-image: url("../my-library-desk-x2.c387cf2a.jpg");
  }
}

.header-library__btn {
  width: 130px;
  height: 44px;
  border: 1px solid #fff;
}

@media screen and (min-width: 768px) {
  .header-library__btn {
    width: 152px;
  }

  .header-library__btn + .header-library__btn {
    margin-left: 20px;
  }
}

.header-library__btn-wrap {
  transition: translate(-50%, -50%);
  position: absolute;
  top: 60%;
  left: 15%;
}

@media screen and (min-width: 768px) {
  .header-library__btn-wrap {
    left: 30%;
  }
}

@media screen and (min-width: 1280px) {
  .header-library__btn-wrap {
    left: 40%;
  }
}

.header__logo--title {
  display: none;
}

@media screen and (min-width: 768px) {
  .header__logo--title {
    color: #fff;
    font-size: 30px;
    font-weight: 500;
    line-height: 1.16;
    display: inline-flex;
  }
}

.header-list {
  justify-content: center;
  align-items: center;
  display: flex;
}

.header-list__item + .header-list__item {
  margin-left: 40px;
}

.header-library__btn {
  color: #fff;
  background-color: #0000;
}

.header-library__btn:hover, .header-library__btn:focus {
  background: #ff6b08;
  border: none;
  box-shadow: 0 8px 43px #ff6b0199;
}

.button {
  text-transform: uppercase;
  border-radius: 5px;
  padding-top: 13px;
  padding-bottom: 13px;
  font-family: Roboto;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  line-height: 1.33333;
}

